* {
 box-sizing: border-box;
}

body {
  font-family:'Segoe UI', 'Open Sans', 'Helvetica Neue', sans-serif;
  background-color: #fafafa;
  color: #151414;
  line-height: 1.5;
  margin: 0;
  padding: 0;

  overflow-y: scroll;
}

ul,
ol {
  list-style: none;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  margin: 0;
  padding: 0;
}

.search-form__wrapper {
  display: flex;
  justify-content: center;
  align-items: center;

  height: 60px;
}

.search-form__background {
  position: fixed;

  height: 60px;
  width: 100%;

  background-color: #5e22e9;
}

.search-form {
  position: fixed;
}

.input {
  display: inline-flex;

  width: 400px;
  height: 30px;
  padding-left: 10px;

  border: none;
  outline: none;
  border-radius: 3px;
}

.search-btn {
  position: absolute;
  top: 0;
  right: 0;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 60px;
  height: 100%;
  border-radius: 3px;
  margin: 0;
  padding: 0;

  border: none;
  cursor: pointer;
  background-color: #aca6e3;
}

.search-btn:hover {
  background-color: #d9bdf8;
}

.gallery {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  margin-bottom: 15px;
}

.photo-card {
  width: 350px;
  margin: 10px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.2);
}

.photo-card__img {
  display: block;
  width: 100%;
  height: 230px;
  object-fit: cover;
}

.info {
  display: flex;
  justify-content: space-around;
  align-items: center;

  width: 100%;
  height: 60px;
  font-size: 14px;
}

.info-item {
  text-align: center;
}

.end-search__info {
  display: none;
  text-align: center;
  color: #c1bebe;
  font-size: 20px;

  margin-top: 0;
  margin-bottom: 25px;
}